import React, { Component } from 'react';
import './calc.css';
import 'react-input-range/lib/css/index.css';
import OwlCarousel from 'react-owl-carousel';
import AnimatedNumber from 'react-animated-number';
import InputRange from 'react-input-range';
import ToggleButton from 'react-toggle-button';
class Calculator extends Component {
    constructor(props) {
    super(props);
    this.getPercentValue = this.getPercentValue.bind(this);
    this.state = {
	   save:[12.5,13,14,18,18],
     unsave:[14,15,16,21,21],
     saveState:false,
      value: 0,
      curPer:12.5,
    };
  }
  getPercentValue(value = this.state.value){
    let percType;
    this.state.saveState ? percType = this.state.save : percType = this.state.unsave;
    this.setState({curPer : percType[value] , value : value});
    return percType[value];
  }
  render() {
  	return(
  		<div className="calculator">
      <div className="calculator__text">
      <OwlCarousel 
  className="owl-theme"
  loop margin={10}
  items={1}
>
<div className="owlSlide">
  <h3>  </h3>
  <p>        .              .  ?      6       .           !
            .          .       ,       . ,           . 
         ,     ,   ,      .       ,   . 
    ,    ,    .          ,      - .       ,        2-2,5%! 
       ,        . ..    12,5%+2,5%=15%,         .
        ,         !</p>
</div>
<div className="owlSlide">
  <h3>  </h3>
  <p>     ,        1   2 .         .       : 1   5 , 1   15 , 1   30 , 1   60 , 1   90 , 1   180 , 1   ,   .        ,          .    :     .         13% ;    15% .       ,   24        .   Firbo Capital      ,     .        ,     , ,    .    ,        !         !</p>
</div>
<div className="owlSlide">
  <h3>  </h3>
  <p>               .       2  3 .          .       : 1   5 , 1   15 , 1   30 , 1   60 , 1   90 , 1   180 , 1   , 1   2 ,   .         ,    .     Firbo Capital             .          ,     . :  , , , ,      .         :      .       14% /    16% .   ,           .        ,       .     Firbo Capital         ! 
  
  18% /   21%
      Firbo Capital,     ,    -        .       3   . ,     ,    .       : 1   5 , 1   15 , 1   30 , 1   60 , 1   90 , 1   180 , 1   , 1   2 ,   .       18% /    21% .      ,     24          .        ?  !   Firbo Capital      1996 ,          ,        .      .      ,    ,          .  ,    , /,  .      ,     Firbo Capital.      ,         !</p>
</div>
</OwlCarousel>
      </div>
      <div className="caclulator__info">
      <h2>:</h2><h1>
        <AnimatedNumber component="text" value={this.state.curPer}
            style={{
                transition: '0.8s ease-out',
                fontSize: 48,
                transitionProperty:
                    'background-color, color, opacity'
            }}
            frameStyle={perc => (
                perc === 100 ? {} : {backgroundColor: '#ffeb3b'}
            )}
            duration={300}
            stepPrecision={1}/>
      %</h1>
      <h2>:</h2>
      <InputRange
        
        maxValue={4}
        minValue={0}
        value={this.state.value}
        onChange={value => this.getPercentValue(value)} />
        <h2>  :</h2>
        <ToggleButton
  inactiveLabel={''}
  activeLabel={''}
  value={this.state.saveState}
  onToggle={(value) => {
    this.setState({
      saveState: !value,
    }
    );
   this.getPercentValue();
  }} />

      </div>
      </div>
  		)
  }
}
export default Calculator;


